import * as THREE from './three.min.js';
import OrbitControls from './OrbitControls.js';
import * as AOS from 'aos/dist/aos.js';

var starViewer = {

  // variables
  camera: false,
  controls: false,
  scene: false,
  renderer: false,
  container: false,
  textlabels: [],

  onReady: function() {
    this.scene = new THREE.Scene();

    this.renderer = new THREE.WebGLRenderer();
    this.renderer.setClearColor(0x004477);
    this.renderer.setPixelRatio(window.devicePixelRatio);
    this.renderer.setSize(window.innerWidth, window.innerHeight);

    this.container.appendChild(this.renderer.domElement);

    // Set a close distance to hide close object
    this.camera = new THREE.PerspectiveCamera(40, window.innerWidth / window.innerHeight, 550, 10000);
    this.camera.position.set( 0, 10, 400 );

    /// Create geometry for panorama photo
    var sphereGeo = new THREE.SphereBufferGeometry(500, 70, 60);

    // invert geometry normal
    sphereGeo.scale(-1, 1, 1);

    // Define panorama image
    var panoMat = new THREE.MeshBasicMaterial({
        map: new THREE.TextureLoader().load('assets/img/texture/pano2.jpg')
    });

    var panoMesh = new THREE.Mesh( sphereGeo, panoMat );
    this.scene.add(panoMesh);

    // Added Orbit Controls camera
    if(isMobile()) {
      console.log("on phone");
      this.controls = new THREE.DeviceOrientationControls(this.camera);
    } else {
      console.log("on desktop");
      this.controls = new THREE.OrbitControls(this.camera, this.renderer.domElement);
    }
    // this.controls = new THREE.OrbitControls(this.camera, this.renderer.domElement);
    this.controls.enableDamping = true;
    this.controls.dampingFactor =  0.25;
    this.controls.enableZoom = false;
    this.controls.enablePan = false;
    this.controls.enableKeys = false;

    //// world

    // Create base mesh Cylinder geometry
    var geometry = new THREE.SphereBufferGeometry(0, 10, 30);
    var defaultMat = new THREE.MeshBasicMaterial({
      color: 0xffffff,
    });

    // Delcare a list for hotspots
    var hotspotList = [
      { 'name': 'car-1', 'pos': {'x': 350, 'y': 100, 'z': 220}, 'title': 'About', 'description': 'Learn more about me.', 'func': 'aboutModal.open()'},
      { 'name': 'car-2', 'pos': {'x': -400, 'y': 50, 'z': 20}, 'title': 'Contact', 'description': 'Say Hello! Send an Email.', 'func': 'contactModal.open()'},
      { 'name': 'car-3', 'pos': {'x': 400, 'y': 10, 'z': 10}, 'title': 'Portfolio', 'description': 'Check out my works.', 'func': 'portfolioModal.open()'},
      { 'name': 'car-4', 'pos': {'x': -350, 'y': 20, 'z': -250}, 'title': 'demo', 'description': 'Lorem Ipsum Polum.', 'func': 'demoModal.open()'},
    ];


    // Create mesh basic materials, i defines the number of cylinder
    for (var i = 0; i < hotspotList.length; i++) {
      var selectedGeo = hotspotList[i];
      var mesh = new THREE.Mesh(geometry, defaultMat);
      mesh.name = selectedGeo.name || '';

      // mesh.position.x = (Math.random() - 0.5) * 1000;
      mesh.position.x = selectedGeo.pos.x;
      mesh.position.y = selectedGeo.pos.y;
      mesh.position.z = selectedGeo.pos.z;
      mesh.updateMatrix();
      mesh.matrixAutoUpdate = false;
      this.scene.add(mesh);

      var text = this._createTextLabel();
      // text.setHTML("Label "+i);
      console.log('open', selectedGeo.func)
      text.setHTML(
        `<a class="three-hotspot-item hvr-outline-out" onclick="${selectedGeo.func}"><i class="fa fa-info-circle"></i></a>
        <div class="three-hotspot-infobox">
          <h2 class="infobox-title">${selectedGeo.title}</h2>
          <hr>
          <h4 class="infobox-description" >${selectedGeo.description}</h4>
        </div>`
      );

      text.setParent(mesh);
      this.textlabels.push(text);
      console.log(this.textlabels);
      this.container.appendChild(text.element);
    }

    //
    // animate
    //

    var _this = this;
    var animate = function() {
      requestAnimationFrame(animate);
      _this.controls.update();
      _this._render();
    }
    animate();
  },

  onResize: function() {
    this.camera.aspect = window.innerWidth / window.innerHeight;
    this.camera.updateProjectionMatrix();
    this.renderer.setSize(window.innerWidth, window.innerHeight);
  },

  _render: function() {
    for(var i=0; i<this.textlabels.length; i++) {
      this.textlabels[i].updatePosition();
    }
    this.renderer.render(this.scene, this.camera);
  },

  _createTextLabel: function() {
    var div = document.createElement('div');
    div.className = 'three-hotspot';
    div.style.position = 'absolute';
    div.style.width = 100;
    div.style.height = 100;
    div.innerHTML = '';
    div.style.top = -1000;
    div.style.left = -1000;

    // var infoIcon = `<a class="three-hotspot-item hvr-outline-out"><i class="fa fa-info-circle"></i></a>`;''
    // div.innerHTML += infoIcon;

    var _this = this;

    return {
      element: div,
      parent: false,
      position: new THREE.Vector3(0,0,0),
      setHTML: function(html) {
        this.element.innerHTML += `${html}`;
      },
      setParent: function(threejsobj) {
        this.parent = threejsobj;
      },
      updatePosition: function() {
        if(parent) {
          this.position.copy(this.parent.position);
        }
        var coords2d = this.get2DCoords(this.position, _this.camera);
        this.element.style.left = coords2d.x + 'px';
        this.element.style.top = coords2d.y + 'px';
      },
      get2DCoords: function(position, camera) {
        var vector = position.project(camera);
        vector.x = (vector.x + 1)/2 * window.innerWidth;
        vector.y = -(vector.y - 1)/2 * window.innerHeight;
        return vector;
      },
    };
  }
};

starViewer.container = document.getElementById('container');
starViewer.onReady();

var overlay = document.getElementsByClassName('overlay')[0];

var modalCreater = {

  modalList: [],

  onReady: function() {

    var modalListID = [
      {'id': 'aboutModal', 'name': 'about-modal', 'endPos': {'x': 0, 'y': 0}},
      {'id': 'contactModal', 'name': 'contact-modal', 'endPos': {'x': '16.5%', 'y': '10%'}},
      {'id': 'portfolioModal', 'name': 'portfolio-modal', 'endPos': {'x': '25%', 'y': '5%'}},
      {'id': 'item', 'name': 'check'},
      {'id': 'item2', 'name': 'mate'}
    ];

    for(var i = 0; i < modalListID.length; i++) {
      // !!modalListID[i].id ? this.modalList.push(modalListID[i].name) : null;
      !!modalListID[i].id ? window[modalListID[i].id] = this._createModalBox(modalListID[i].name, modalListID[i].endPos) : null;
    }

    // for(var i = 0; i < this.modalList.length; i++) {
    //   window[modalListID[i].id] = this._createModalBox(this.modalList[i]);
    // }
  },

  _createModalBox: function(id, endPos={'x':0, 'y':0}) {
    var item = document.getElementById(id);
    console.log('item', item, 'endpos', endPos)
    if(!!item) {
      var position = {'x': item.style.right, 'y': item.style.top}
      var _this = this;
      return {
        element: item,
        pos: position,
        close: function() {
          item.style.visibility = 'hidden';
          item.style.opacity = "0";
          overlay.style.display = 'none';
          // item.style.right = this.pos.x;
          // item.style.top = this.pos.y;
          item.classList.remove("active");
          setTimeout(function() {
            console.log('1')
            AOS.refresh(true);
          }, 500);
        },
        open: function() {
          console.log("open")
          item.style.visibility = 'visible';
          item.style.opacity = "1";
          overlay.style.display = 'block';
          // item.style.right = endPos.x;
          // item.style.top = endPos.y;
          item.classList.add("active");

          setTimeout(function() {
            console.log('1')
            AOS.refresh(true);
          }, 500);
        }
      }
    }
  }
};

modalCreater.onReady();
$('.portfolio-image-container').on('scroll', function() {
  AOS.refresh(true);
});


function isMobile() {
  if (navigator.userAgent.match(/Android/i)
      || navigator.userAgent.match(/webOS/i)
      || navigator.userAgent.match(/iPhone/i)
      || navigator.userAgent.match(/iPad/i)
      || navigator.userAgent.match(/iPod/i)
      || navigator.userAgent.match(/BlackBerry/i)
      || navigator.userAgent.match(/Windows Phone/i)
  ) {
      return true;
  }
  else {
      return false;
  }
}

window.addEventListener('resize', function() {
  starViewer.onResize();
}, false);
